	module.exports = "<h2 id=overview>Overview</h2> <p>Always add a modifier class to add color to the button.</p> <h2 id=accessibility>Accessibility</h2> <p>An active state button will appear pressed when active. There’s no need to add a class to <code>&lt;button&gt;</code>s as they use a pseudo-class. However, if you are using a button as a toggle, you can still force the same active appearance with <code>pf-is-active</code> (and include the <code>aria-pressed=&quot;true&quot;</code>) should you need to replicate the state programmatically.</p> <p>Disabled buttons should include the <code>disabled=&quot;disabled&quot;</code> attribute to indicate the state of the element to assistive technologies.</p> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Usage</th> </tr> </thead> <tbody> <tr> <td><code>.pf-c-button</code></td> <td><strong>Applied to:</strong> <code>&lt;button&gt;</code></td> <td><strong>Outcome:</strong> Initiates a button</td> <td><strong>Required:</strong> Yes</td> <td><strong>Remarks:</strong> Always use it with a modifier class.</td> </tr> <tr> <td><code>.pf-is-primary</code></td> <td><strong>Applied to:</strong> <code>.pf-c-button</code></td> <td><strong>Outcome:</strong> Modifies for primary styles</td> <td><strong>Required:</strong> No</td> </tr> <tr> <td><code>.pf-is-secondary</code></td> <td><strong>Applied to:</strong> <code>.pf-c-button</code></td> <td><strong>Outcome:</strong> Modifies for secondary styles</td> <td><strong>Required:</strong> No</td> </tr> <tr> <td><code>.pf-is-secondary-alt</code></td> <td><strong>Applied to:</strong> <code>.pf-c-button</code></td> <td><strong>Outcome:</strong> Modifies for alternate secondary styles</td> <td><strong>Required:</strong> No</td> </tr> <tr> <td><code>.pf-is-tertiary</code></td> <td><strong>Applied to:</strong> <code>.pf-c-button</code></td> <td><strong>Outcome:</strong> Modifies for tertiary styles</td> <td><strong>Required:</strong> No</td> </tr> <tr> <td><code>.pf-is-danger</code></td> <td><strong>Applied to:</strong> <code>.pf-c-button</code></td> <td><strong>Outcome:</strong> Modifies for danger styles</td> <td><strong>Required:</strong> No</td> </tr> <tr> <td><code>.pf-is-block</code></td> <td><strong>Applied to:</strong> <code>.pf-c-button</code></td> <td><strong>Outcome:</strong> Creates a block level button</td> <td><strong>Required:</strong> No</td> </tr> <tr> <td><code>.pf-is-hover</code></td> <td><strong>Applied to:</strong> <code>.pf-c-button</code></td> <td><strong>Outcome:</strong> Forces display of the hover state of the button</td> <td><strong>Required:</strong> No</td> <td><strong>Notes:</strong> This state is primarily for demonstration purposes and would not normally be used in lieu of the <code>:hover</code> pseudo-class. </td> </tr> <tr> <td><code>.pf-is-active</code></td> <td><strong>Applied to:</strong> <code>.pf-c-button</code></td> <td><strong>Outcome:</strong> Forces display of the active state of the button</td> <td><strong>Required:</strong> No</td> <td><strong>Notes:</strong> This state is primarily for demonstration purposes and would not normally be used in lieu of the <code>:active</code> pseudo-class. </td> </tr> <tr> <td><code>.pf-is-focus</code></td> <td><strong>Applied to:</strong> <code>.pf-c-button</code></td> <td><strong>Outcome:</strong> Forces display of the focus state of the button</td> <td><strong>Required:</strong> No</td> <td><strong>Notes:</strong> This state is primarily for demonstration purposes and would not normally be used in lieu of the <code>:focus</code> pseudo-class. </td> </tr> </tbody> </table> ";

